/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import logo from "./assets/img/LOGO-PHILA.png";
import { Button, Col, Checkbox, Form, Input, Select, Row } from "antd";
import { Snackbar } from "@mui/material";
import MuiAlert from "@mui/material/Alert";
import axios from "axios";
const { Option } = Select;

const AlertInfos = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const App = () => {
  const [form] = Form.useForm();
  const [open, setOpen] = useState(false);
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState("");
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [street, setStreet] = useState("");
  const [postalCode, setPostalCode] = useState("");
  const [city, setCity] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [gender, setGender] = useState("");

  const onFinish = (e) => {
    if (
      firstname &&
      lastname &&
      street &&
      postalCode &&
      city &&
      email &&
      phone &&
      gender
    ) {
      axios
        .post(`${process.env.REACT_APP_API_URL}/members`, {
          firstname,
          lastname,
          email,
          phone,
          gender,
        })
        .then(() => {
          setOpen(true);
          form.resetFields();
        })
        .catch((err) => {
          setError(err.response.data.message);
          setOpenError(true);
        });
    }
  };

  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenError(false);
    setOpen(false);
  };

  return (
    <div className="text-center form-container">
      <img src={logo} alt="logo" className="App-logo container mb-3" />
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinish}
        initialValues={{
          prefix: "33",
        }}
        style={{
          maxWidth: 600,
        }}
        scrollToFirstError
        className="container pt-3 mt-3 form col-11"
      >
        <Form.Item
          name="lastname"
          label="Nom"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre nom !",
              whitespace: true,
            },
          ]}
          className=""
        >
          <Input
            placeholder="Votre nom"
            value={lastname}
            onChange={(e) => setLastname(e.target.value)}
            className="form-input"
          />
        </Form.Item>

        <Form.Item
          name="firstname"
          label="Prnom"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre prnom!",
              whitespace: true,
            },
          ]}
        >
          <Input
            placeholder="Votre prnom"
            value={firstname}
            onChange={(e) => setFirstname(e.target.value)}
            className="form-input"
          />
        </Form.Item>

        <Form.Item
          label="Adresse"
          // extra="We must make sure that your are a human."
        >
          <Row gutter={8}>
            <Col span={12}>
              <Form.Item
                name="Rue"
                rules={[
                  {
                    required: true,
                    // message: "Please input the captcha you got!",
                  },
                ]}
              >
                <Input
                  placeholder="Rue"
                  className="form-input"
                  value={street}
                  onChange={(e) => setStreet(e.target.value)}
                />
              </Form.Item>
            </Col>

            <Col span={5}>
              <Form.Item
                name="Code postal"
                rules={[
                  {
                    required: true,
                    // message: "Please input the captcha you got!",
                  },
                ]}
              >
                <Input
                  placeholder="CP"
                  className="form-input"
                  value={postalCode}
                  onChange={(e) => setPostalCode(e.target.value)}
                />
              </Form.Item>
            </Col>

            <Col span={7}>
              <Form.Item
                name="Ville"
                rules={[
                  {
                    required: true,
                    // message: "Please input the captcha you got!",
                  },
                ]}
              >
                <Input
                  placeholder="Ville"
                  className="form-input"
                  value={city}
                  onChange={(e) => setCity(e.target.value)}
                />
              </Form.Item>
            </Col>
          </Row>
        </Form.Item>

        <Form.Item
          name="email"
          label="E-mail"
          rules={[
            {
              type: "email",
              message: "Email incorrecte",
            },
            {
              required: true,
              message: "Veuillez entrer votre adresse mail!!",
            },
          ]}
        >
          <Input
            placeholder="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="form-input"
          />
        </Form.Item>

        <Form.Item
          name="phone"
          label="Tlphone"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre tlphone!",
            },
          ]}
        >
          <Input
            style={{
              width: "100%",
            }}
            onChange={(e) => setPhone(e.target.value)}
            placeholder="0123456789"
            className="form-input"
            value={phone}
          />
        </Form.Item>

        <Form.Item
          name="gender"
          label="Genre"
          rules={[
            {
              required: true,
              message: "Veuillez slectionner votre genre!",
            },
          ]}
        >
          <Select
            value={gender}
            placeholder="Votre genre"
            onChange={(e) => setGender(e)}
            popupClassName="form-input"
            // style={{ backgroundColor: "transparent" }}
          >
            <Option value="Homme">Homme</Option>
            <Option value="Femme">Femme</Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="agreement"
          valuePropName="checked"
          rules={[
            {
              validator: (_, value) =>
                value
                  ? Promise.resolve()
                  : Promise.reject(new Error("Should accept agreement")),
            },
          ]}
          {...tailFormItemLayout}
        >
          <Checkbox>
            J'accepte les{" "}
            <a href="#" style={{ color: "white", textDecoration: "none" }}>
              conditions d'utilisation
            </a>
          </Checkbox>
        </Form.Item>

        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit" className="btn-submit">
            Envoyer
          </Button>
        </Form.Item>
      </Form>

      <Snackbar open={open} autoHideDuration={6000} onClose={handleCloseAlert}>
        <AlertInfos
          onClose={handleCloseAlert}
          severity="success"
          sx={{ width: "100%" }}
        >
          Envoy avec succes !
        </AlertInfos>
      </Snackbar>

      <Snackbar
        open={openError}
        autoHideDuration={6000}
        onClose={handleCloseAlert}
      >
        <AlertInfos
          onClose={handleCloseAlert}
          severity="error"
          sx={{ width: "100%" }}
        >
          {error && error}
        </AlertInfos>
      </Snackbar>
    </div>
  );
};

export default App;
